}
.ButtonText {
  color: #174087;
}
.ButtonText2 {
  color: #72BE44;
}
.Button2 {
  color: white;
  background-color: #72BE44;
}
.Button_Eliminar {
  color: white;
  background-color: #b22222;
}
.fondoForm {
  background-color: gray;
}
